<template>
  <div class="max-card">
    <div class="max-card-box">
      <span class="max-card-name">{{name}}</span>
      <canvas id="canvas"></canvas>
      <span class="card-tips">二维码7天内有效，重新进入将更新</span>
    </div>
  </div>
</template>
<script>
import QRCode from 'qrcode'
export default {
  name: 'MaxCard',
  data () {
    const item = localStorage.getItem('chatItem')
    return {
      name: (item && JSON.parse(item).name) || {}
    }
  },
  mounted () {
    this.useqrcode()
  },
  methods: {
    useqrcode () {
      let canvas = document.getElementById('canvas')
      QRCode.toCanvas(canvas, 'https://blog.csdn.net/weixin_42890953/article/details/82776760', error => {
        if (error) console.error(error)
      })
    }
  }
}
</script>
<style lang="less">
  .max-card {
    .max-card-name {
      padding-top: 40px;
      font-size: 18px;
      color: #000;
    }
    .card-tips {
      padding-bottom: 20px;
    }
    #canvas {
      width: 280px !important;
      height: 280px !important;
    }
    .max-card-box {
      position: absolute;
      border-radius: 4px;
      width: 338px;
      height: 400px;
      top: 50%;
      left: 50%;
      margin-left: -169px;
      margin-top: -200px;
      background: #fff;
      display: flex;
      align-items: center;
      justify-content: space-around;
      flex-direction: column;
    }
  }
</style>
